@use "../abstract/breakpoints" as *;
@use "../abstract/colors" as *;
@use "../abstract/fonts" as *;
@use "../abstract/functions" as *;
@use "../abstract/mixins" as *;



.hero{
    position: relative;
    background: $clr-primary;
    height: 85dvh;
    @include flex-center-horizontal;
    align-items: center;
    &--home{
        align-items: flex-end;
    }
    &--about{
        height: 60dvh;
    }
    @include breakpoint-sm{
        height: 60dvh;
    }
    @include breakpoint-xl{
        height: 70dvh;
    }

    &__inner{
        position: relative;
        background: $clr-primary;
        height: 100dvh;
        @include flex-center;
        // @include breakpoint-sm{
        //     height: 70dvh; 
        // }
    }

    &__subTitle{
        width: min(100%, 52ch);
    }
}

.hero-span{
    position: relative;

    &::after{
        content:url("../assets/images/line-orange.svg");
        position: absolute;
        left: 0;
        bottom: -10px;
        display: none;
        @include breakpoint-sm{
            display: block;
        }
    }
}

.hero-container{
    position: absolute;
    bottom:0;
}

.hero-card{
    position: absolute;
    bottom:0;
    right: 0;
    width: min(85%, rem(345));
    padding:em(16);
    border-top-left-radius:26px;
    background-color: $clr-tertiary;
    &__header{
        display: flex;
        flex-direction: column;
        span{
            color:$clr-primary;
        }
        .name{
            font-family:$ff-secondary;
            font-size: rem(16);
            @include breakpoint-sm{
                font-size: rem(18);
            }
            @include breakpoint-md{
                font-size: rem(20);
            }
        }
        .designation{
            font-size: rem(14);
            font-family: $ff-secondary;
            @include breakpoint-sm{
                font-size: rem(16);
            }
        }
    }
}


// ==== Insurance Plans ====
.insurance-plans{
    .card-text{
        font-size: rem(16);
        line-height: 1.5;
    }
    small{
        font-size: rem(14);
        line-height: 1.71;
    }
}


// ==============
.card-container{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(rem(270), 1fr));
    gap: rem(64) rem(24);
    @include breakpoint-xl{
        grid-template-columns: repeat(auto-fill, minmax(rem(224), 1fr));
        gap: rem(36);
    }
    @include breakpoint-xxxl{
        grid-template-columns: repeat(auto-fill, minmax(rem(312), 1fr));
        gap: rem(36);
    }
}

.card{
    display: flex;
    flex-direction: column;
}

.card__body{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding:em(22) em(12);
    border: solid 1px #d8d8d8;
    flex-grow: 1;
}


// ======= Steps
.steps{
    &__content{
        display: flex;
        gap:rem(16);
        @include breakpoint-sm{
            gap:rem(32);
        }
    }
    &__subTitle{
        font-size: rem(16);
        line-height: 1.5;
    }

}


.testimonials-card{
    background: $clr-tertiary;
    padding:em(24) em(16);
    @include border-radius(8px);

    @include breakpoint-sm{
        padding:em(24);
    }

    &__header{
        @include flex-center-vertical;
        gap:rem(16);

        figure{
            @include dimensions(rem(54), rem(54));
            @include border-radius(50%);
            background: $clr-primary;
            flex-shrink: 0;
        }
    }

    .testimonials-desc{
        font-size: rem(16);
        line-height: 1.5;
        color:$clr-black;
    }
}



.counter{
    @include flex-center;
    font-size: clamp(rem(28), 4vw + 1rem, rem(52));
    line-height: 1.08;
    font-family: $ff-primary;
}

.distinctive-card{
    max-width: 80%;
    margin: 0 auto;
}


.story-timeline{
    background: $clr-white;
    clip-path: ellipse(50% 40% at 50% 85%);
    display: grid;
    place-items: center;
    width: min(100%, rem(950));
    height: 700px;
    padding:1em;
    margin-top: -15rem;
    &__year{
        color:$clr-black;
        font-size: rem(24);
        font-family: $ff-primary;
        @include breakpoint-sm{
            font-size: rem(28);
        }
        @include breakpoint-md{
            font-size: rem(32);
        }
    }

    &__body{
        overflow: hidden;
        p{
            color:$clr-black;
        }
    }
    &__content{
        max-width: 48ch;
        margin-inline: auto;
    }
}